export class ContentManagementComponent implements OnInit {
  constructor(private eeDragDropService: TreeDragDropService, private mainService: MainService) {
    this.data = this.mainService.data
  }
  article = true;
  articles = true;
  searchResult = false;
  welcome = true;
  folders = true;
  dublicate = false;

  collapsFolderFlag = false;
  showDialogDelete;
  id;
  addTagField;
  math = Math;
  searchIn: SelectItem[];
  selectedSearchIn;
  filesTree0;
  questuonSettings = {};
  newFolderName = {};

  data;

  dublicateQuestionData = [
    {
      name: " How do I use the 3D touch?",
      open: false,
      questions: [
        {
          name: "What is the camera resolution?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 8 > Specifications"
        },
        {
          name: "What is the camera resolution?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 7 > Specifications"
        },
        {
          name: "What is the camera resolution?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 6 > Specifications"
        },
        {
          name: "What is the camera resolution?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 5 > Specifications"
        }
      ]
    },
    {
      name: "How to replace the screen?",
      open: false,
      questions: [
        {
          name: "How to replace the screen?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 8 > Specifications"
        },
        {
          name: "How to replace the screen?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 7 > Specifications"
        },
        {
          name: "How to replace the screen?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 6 > Specifications"
        },
        {
          name: "How to replace the screen?",
          path:
            "Content > Tecnical > Phones > iPhone > iPhone 5 > Specifications"
        }
      ]
    }
  ];
  selectedType;
 
  searchText = "";
  selectedFolder;
  selectedText;
  searchResultData = [];
  editPageCount;
  editPageCountValue;
  previewText;
  searchTextChange(e) {
    this.searchResultData = [];
    this.data[this.selectedType].forEach((element, index) => {
      if (element.type === "text") {
        if (element.text.includes(e)) {
          this.searchResultData.push({
            type: "text",
            label: element.title,
            element: element
          });
        }

        for (let item of element.tegs) {
          if (item.includes(e)) {
            this.searchResultData.push({
              type: "teg",
              label: item,
              element: element
            });
          }
        }
      }
    });
    console.log(e, this.searchResultData);

    setTimeout(() => {
      var divs = document.querySelectorAll(".context");

      [].forEach.call(divs, function(div) {
        console.log(div);
        var options = {};
        var markInstance = new Mark(div);

        markInstance.unmark({
          done: function() {
            markInstance.mark(e, options);
          }
        });
      });
    }, 100);
  }
  articlesDataArr;
  onNodeSelect(e, i) {
    console.log(e);
    this.selectedType = e.node.label;
    this.selectedFolder = e.node;
    this.welcome = false;

    this.search(this.data, e.node.id);
  }

  renameNode(arr, id, name) {
    for (let item of arr) {
      if (item.id === id) {
        item.label = name;
      }
      if (item.children) {
        this.renameNode(item.children, id, name);
      }
    }
  }

  search(arr, id) {
    for (let item of arr) {
      if (item.id === id) {
        if (item.children) {
          this.articlesDataArr = item.children;
        }
      }
      if (item.children) {
        this.search(item.children, id);
      }
    }
  }

  onNodeUnselect(e) {
    this.selectedFolder = null;
  }
  selectType(item, index) {
    if (item.type === "text") {
      this.editPageCount = 5;
      this.selectedText = item;
      console.log(item, "item");
      this.previewText = item.text.split(".");
      this.previewText = this.previewText.slice(
        this.editPageCount - 5,
        this.editPageCount
      );
      this.editPageCountValue = this.math.ceil(
        this.selectedText.text.split(".").length / 5
      );
    }
    this.id = item.id;
  }
  selectedTextAction(flag) {
    console.log(this.editPageCount, "this.editPageCount");

    if (flag === "prev" && this.editPageCount > 5) {
      this.editPageCount = this.editPageCount - 5;
    } else if (
      flag === "next" &&
      this.editPageCount < this.selectedText.text.split(".").length
    ) {
      this.editPageCount = this.editPageCount + 5;
    }
    this.previewText = this.selectedText.text
      .split(".")
      .slice(this.editPageCount - 5, this.editPageCount);
  }

  addFolder() {
    let id = Math.floor(Math.random() * Math.floor(1000000));
    if (this.selectedFolder) {
      if (!this.selectedFolder.children) {
        this.selectedFolder.children = [];
      }
      this.selectedFolder.expanded = true;
      let obj = {
        label: "New folder",
        data: "Node 0",
        type: "folder",
        expandedIcon: "fa fa-folder-open",
        collapsedIcon: "fa fa-folder",
        id: id
      };
      this.selectedFolder.children.push(obj);
      this.pushInData(this.selectedFolder.id, obj);
    } else {
      this.filesTree0.push({
        label: "New folder",
        data: "Node 0",
        type: "folder",
        expandedIcon: "fa fa-folder-open",
        collapsedIcon: "fa fa-folder",
        id: id
      });
      this.data.push({
        id: id,
        label: "New folder",
        data: "Node 0",
        type: "folder",
        expandedIcon: "fa fa-folder-open",
        collapsedIcon: "fa fa-folder"
      });
    }
  }
  arr;
  ngOnInit() {
    this.arr = this.deepCopy(this.data);

    this.hideTest(this.arr);
    this.filesTree0 = this.arr;

    this.searchIn = [
      { label: "Current Folder", value: "Current Folder" },
      { label: "All Folder", value: "All Folder" }
    ];
    this.selectedSearchIn = this.searchIn[0].label;
  }

  pushInData(id, obj) {
    let count = 0;
    let removeArr = [];
    for (let i = 0; i < this.data.length; i++) {
      if (this.data[i].children) {
        this.hideTest(this.data[i].children);
      }
      if (this.data[i].id === id) {
        this.data[i].children.push(obj);
      }
    }
  }

  hideTest(arr) {
    let count = 0;
    let removeArr = [];
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].children) {
        this.hideTest(arr[i].children);
      }
      if (arr[i].type === "text") {
        removeArr.push(i);
      }
    }

    for (let i of removeArr) {
      arr.splice(i - count, 1);
      count += 1;
    }
  }

  openDubicate() {
    this.articles = false;
    this.folders = false;
    this.dublicate = true;
    this.welcome = false;
  }
  backContent() {
    this.articles = true;
    this.folders = true;
    this.dublicate = false;
    this.searchResult = false;
  }
  openSearch() {
    this.article = true;
    this.articles = false;
    this.searchResult = true;
    this.folders = false;
    this.dublicate = false;
  }

  closeSearch() {
    this.article = true;
    this.articles = true;
    this.searchResult = false;
    this.folders = true;
    this.dublicate = false;
  }

  dataDelete;
  indexDelete;
  deleteFlag
  detaleItem(data?, index?, event?, flag?) {
      if (data !== null || index !== null) {
        this.dataDelete = data;
        this.indexDelete = index;
        this.deleteFlag = flag;
        this.showDialogDelete = true;
      } else {
        if (!event) {
          this.showDialogDelete = false;
        } else {
          if (this.deleteFlag === 'article'){
            this.removeFolder(this.data, this.indexDelete);
            this.removeFolder(this.arr, this.indexDelete);
            this.showDialogDelete = false;
            this.deleteFlag = null;
          } else {
            this.dataDelete.splice(this.indexDelete, 1);
            this.showDialogDelete = false;
          }
        }
      }
  }

  onDrop(e) {
    console.log(e, 11111111);
  }

  removeFolder(arr, id) {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].children) this.removeFolder(arr[i].children, id);
      if (arr[i].id === id) {
        arr.splice(i, 1);
      }
    }
  }

  addTags(data, value) {
    data.push(value);
    console.log(data, "data");
    this.addTagField = "";
  }
  deleteArticles() {
    for (let item of this.articlesDataArr) {
      console.log(item)
      if (item.checked) {
        setTimeout(() => {
          this.removeFolder(this.data, item.id);
          this.removeFolder(this.arr, item.id);
        }, 1000);
   
      }
    }
  }
  deleteSearchResults() {
    this.searchResultData = this.searchResultData.filter(item => {
      console.log(item, "item");
      if (item.element.checked === false) {
        return true;
      }
    });
  }
  deleteQuestion() {
    let count = 0;
    for (const key in this.questuonSettings) {
      if (this.questuonSettings[key]) {
        this.selectedText.questins.splice(+key - count, 1);
        count++;
      }
    }
    this.questuonSettings = {};
  }
  expandetTree() {
    this.collapsFolderFlag = !this.collapsFolderFlag;
    this.filesTree0.forEach(node => {
      this.expandRecursive(node, this.collapsFolderFlag);
    });
  }

  private expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }

  deepCopy(obj) {
    if (typeof obj == "object") {
      if (Array.isArray(obj)) {
        var l = obj.length;
        var r = new Array(l);
        for (var i = 0; i < l; i++) {
          r[i] = this.deepCopy(obj[i]);
        }
        return r;
      } else {
        let r = Object.assign({}, obj);
        for (var k in obj) {
          r[k] = this.deepCopy(obj[k]);
        }
        return r;
      }
    }
    return obj;
  }
}
