import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dialog-folder-name',
  templateUrl: './dialog-folder-name.component.html',
  styleUrls: ['./dialog-folder-name.component.scss']
})
export class DialogFolderNameComponent implements OnInit {
  @Input() title;
  @Input() fieldValue;
  @Output() result = new EventEmitter;
  constructor() { }
  answer(value) {
    this.result.emit(value);
  }
  ngOnInit() {
  }

}